"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'

export default function EmployeeDashboard() {
  const [isTracking, setIsTracking] = useState(false)
  const [timeElapsed, setTimeElapsed] = useState(0)
  const [screenshotInterval, setScreenshotInterval] = useState(30)
  const [idleTime, setIdleTime] = useState(0)
  const [lastScreenshot, setLastScreenshot] = useState<Date | null>(null)

  useEffect(() => {
    let interval: NodeJS.Timeout
    if (isTracking) {
      interval = setInterval(() => {
        setTimeElapsed(prev => prev + 1)
        setIdleTime(prev => prev + 1)
      }, 1000)
    }
    return () => clearInterval(interval)
  }, [isTracking])

  useEffect(() => {
    let screenshotTimer: NodeJS.Timeout
    if (isTracking) {
      screenshotTimer = setInterval(() => {
        setLastScreenshot(new Date())
        setIdleTime(0) // Reset idle time on screenshot
      }, screenshotInterval * 1000)
    }
    return () => clearInterval(screenshotTimer)
  }, [isTracking, screenshotInterval])

  const formatTime = (seconds: number) => {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    const secs = seconds % 60
    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  const handleStartStop = () => {
    setIsTracking(!isTracking)
    if (!isTracking) {
      setIdleTime(0)
    }
  }

  const handleReset = () => {
    setIsTracking(false)
    setTimeElapsed(0)
    setIdleTime(0)
    setLastScreenshot(null)
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Time Doctor</h1>
            <p className="text-gray-600">Employee Time Tracking Dashboard</p>
          </div>
          <div className="flex gap-4">
            <Badge variant={isTracking ? "default" : "secondary"}>
              {isTracking ? "Tracking Active" : "Tracking Stopped"}
            </Badge>
            <Button 
              variant="outline" 
              onClick={() => window.open('/admin', '_blank')}
            >
              Admin Panel
            </Button>
          </div>
        </div>

        {/* Main Timer Card */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-center">Current Session</CardTitle>
          </CardHeader>
          <CardContent className="text-center">
            <div className="text-6xl font-mono font-bold text-blue-600 mb-6">
              {formatTime(timeElapsed)}
            </div>
            <div className="flex justify-center gap-4">
              <Button 
                onClick={handleStartStop}
                size="lg"
                className={isTracking ? "bg-red-600 hover:bg-red-700" : "bg-green-600 hover:bg-green-700"}
              >
                {isTracking ? "Stop Tracking" : "Start Tracking"}
              </Button>
              <Button 
                onClick={handleReset}
                variant="outline"
                size="lg"
              >
                Reset
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Status Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
          <Card>
            <CardHeader>
              <CardTitle className="text-sm font-medium">Screenshot Interval</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{screenshotInterval}s</div>
              <p className="text-xs text-gray-600">Next screenshot in {screenshotInterval - (timeElapsed % screenshotInterval)}s</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-sm font-medium">Idle Time</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-orange-600">{formatTime(idleTime)}</div>
              <p className="text-xs text-gray-600">Time since last activity</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-sm font-medium">Last Screenshot</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-sm font-bold">
                {lastScreenshot ? lastScreenshot.toLocaleTimeString() : "Not taken yet"}
              </div>
              <p className="text-xs text-gray-600">Automatic capture</p>
            </CardContent>
          </Card>
        </div>

        {/* Activity Progress */}
        <Card>
          <CardHeader>
            <CardTitle>Today's Activity</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <div className="flex justify-between text-sm mb-2">
                  <span>Productive Time</span>
                  <span>{formatTime(Math.max(0, timeElapsed - idleTime))}</span>
                </div>
                <Progress value={timeElapsed > 0 ? ((timeElapsed - idleTime) / timeElapsed) * 100 : 0} />
              </div>
              <div>
                <div className="flex justify-between text-sm mb-2">
                  <span>Total Time</span>
                  <span>{formatTime(timeElapsed)}</span>
                </div>
                <Progress value={Math.min((timeElapsed / (8 * 3600)) * 100, 100)} />
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
